@import url(https://fonts.googleapis.com/css2?family=Lato:wght@700&family=Open+Sans:wght@600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300;700&family=Open+Sans:wght@600&family=Roboto&display=swap);
:root {
    --margin-text: 2rem;
}

.home__wrapper {
    padding: 2% 20%;
    background-color: #1E3163;
}

.item__line{
    margin-top: 1em;
    margin-bottom: 1.7em ;
}

.button__container {
    width: 152px;
    padding-top: 3px;
    display: flex;
    justify-content: space-between;

}

.item__wrapper{
    margin-top: 1rem;
    border-radius: 10px;
    background-color: lavender;
    padding-top: 1rem;
    padding-bottom: 1rem;
    display: grid;
}

.item__wrapper h1{
    margin-left: 2rem;
    margin-left: var(--margin-text);
    margin-right: 2rem;
    margin-right: var(--margin-text);
}

.item__paragraph-main {
    padding-top: 1rem;
    padding-bottom: 1rem;
    margin-left: 2rem;
    margin-left: var(--margin-text);
    margin-right: 2rem;
    margin-right: var(--margin-text);
}

.item__wrapper p{
    margin-left: 2rem;
    margin-left: var(--margin-text);
    margin-right: 2rem;
    margin-right: var(--margin-text);

}
.post__wrapper {
    width: 100%;
    border-radius: 10px;
    background-color: lavender;
    margin-bottom: 1rem;
    margin-top: 1rem;
    display: grid;
}

.post__wrapper h2 {
    margin-top: 1rem;
    margin-right: 2rem;
    margin-left: 2rem;
}

.post__wrapper p {
    margin: 0.5rem 2rem;
}

.post__line{
    margin-top: 1em;
    margin-bottom: 1.7em ;
}


.posts__wrapper{
    padding-left: 20%;
    padding-right: 20%;
    background-color: #1E3163;
    display: grid;
    place-items: center;
}

.posts-button__wrapper{
    width: 250px;
    padding-top: 0.5rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.saved-items__wrapper {
    display: grid;
    place-items: center;
    grid-template-columns: repeat(1, 1fr);

}

.saved-posts-h1 {
    color: #C1A1D3;
}


.about__wrapper{
    padding-left: 20%;
    padding-right: 20%;
    display: grid;
    place-items: center;
}

.about__hr{
    margin-top: 0.5rem;
    margin-bottom: 0.5rem;
}

.about__text__wrapper {
    margin-top: 2rem;
    width: 500px;
    background-color: lavender;
    border-radius: 10px;
    padding-bottom: 1rem;
    padding-top: 1rem;
}

.about__h1{
    text-align: center;
}

.about__paragraph_wrapper{
    margin-left: var(--margin-text);
    margin-right: var(--margin-text);

}

.about__ul {
    margin-left: var(--margin-text);
    margin-right: var(--margin-text);
}


.page404__wrapper {
    margin-top: 2rem;
    padding-left: 20%;
    padding-right: 20%;
    text-align: center;
    color: #c1a1d3;

}

.page404__h1{
    font-size: xxx-large;

}
.navbar--desktop {
    font-family: 'Lato', sans-serif;
    padding-top: 1em;
    padding-bottom: 0.25em;
    width: 100%;
    display: grid;
    grid-template-columns: 20% 1fr 3fr 1fr 20%;
    justify-content: center;
    align-items: center;
    background-color: #1E3163;

}

.navbar__logo--desktop {
    justify-self: start;
    margin: 0;
    grid-column: 2;
    font-size: 30px;
}

.navbar__list--desktop {
    justify-self: center;
    grid-column: 3;
    display: flex;
    list-style: none;
    grid-gap: 1em;
    gap: 1em;
    padding: 0;
    margin: 0;
}

.navbar__login--desktop {
    color: #C1A1D3;
    padding: 0.25rem 0;
    justify-self: end;
    grid-column: -3;
}

.navbar__button {
    font-family: 'Open Sans', sans-serif;
    color: #C1A1D3;
    font-size: 1rem;
    padding: 2px;
    background-color: #1E3163;
    border-color: #1E3163;
    border-width: 0;
    text-transform: uppercase;
    cursor: pointer;
}

.navbar__button:hover {
    color: #6E85B2;
}

.navbar__line {
    height: 0.5px;
    background-color: #6E85B2;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    background-color: #1E3163;
}

p {
    font-family: 'Roboto', sans-serif;
}

.picture{
    width: 100%;
}


